ul{
    list-style:none;
}
.movie{
    width:40%;
    height:25%;

    margin-bottom:70px;

    background-color: beige;

    display:flex;
    flex-direction:row;
    
    position:releative;

    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
    border-radius:5px;
}

.movie img{
    width:30%;
    height:50%;

    position:relative;
    bottom:20px;
    left:20px;

    box-shadow: 0 30px 60px -12px rgba(50,50,93,0.25), 0 18px 36px -18px rgba(0,0,0,0.3), 0 -12px 36px -8px rgba(0,0,0,0.025);
}

.movie__data{
    width:50%;

    display:flex;
    flex-direction: column;
    flex-wrap:nowrap;
    align-items: flex-start;

    padding-left:50px;
    padding-top:30px;
}

.movie__data .movie__title{
    text-overflow: ellipsis;
    white-space:nowrap;
    overflow:hidden;
    width:100%;
}

.genres{
    list-style: none;
    padding:0;
    margin:0;
    display:flex;

    color:gray;
}

.genres .genres__genre{
    margin-right:5px;
}